<template>
  <div id="app">
    <icohead></icohead>
    <div class="body">
      <router-view></router-view>
    </div>
    <icofoot></icofoot>
  </div>
</template>

<script>
import icohead from './components/Layout/Header'
import icofoot from './components/Layout/Footer'
export default {
  name: 'app',
  components: {
    icohead,
    icofoot
  }
}
</script>




