<template>
  <div class="footer bg_black">
    <div class="main_width padding_v_20 lineheight_3 row_justify">
      <div class="margin_v_10">
        <img class="width_100" :src="footLogo">
      </div>
      <div class="row">
        <div class="pull_left" style="margin:0 100px;">
          <div class="font_18 white">友情链接</div>
          <div class="font_12" v-for="(t,k) in link">
            <a class="light_white" :href="t">{{ k }}</a>
          </div>
        </div>
        <div class="pull_left">
          <div class="font_18 white">联系我们</div>
          <div class="font_12" v-for="(t,k) in text">{{ k + '：' }}<span class="white">{{ t }}</span></div>
        </div>
      </div>
      <div class="text_center">
        <div class="font_18 white">关注我们</div>
        <img class="margin_v_5" style="width:80px;" :src="qrcode">
        <div class="font_12">扫一扫关注最新ICO信息</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'footer',
  data () {
    return {
      footLogo: require('@/assets/images/foot_logo.png'),
      qrcode: require('@/assets/images/qrcode.png'),
      link: {'保全网': 'https://www.baoquan.com', '千信网': '', '亚欧大数据': ''},
      text: {'媒体合作': 'hz@baoquan.com', 'ICO爆料': 'hz@baoquan.com'}
    }
  }
}
</script>

<style type="text/css">
  a.light_white:hover{
    color:#fff
  }
</style>



