<template>
  <div :class="['header', {'fixed white_nav':!$route.path.includes('Detail')}]">
    <div class="user_info h_justify main_width">
      <div class="width_100">
        <img :src="logo" v-if="$route.path.includes('Detail')">
        <img :src="fixedLogo" v-else>
      </div>
      <div class="flex_reverse margin_v_15">
        <div class="padding_v_10 width_100 text_center hover" v-for="(n,k) in nav">
          <router-link :to="n" :class="[{black: ($route.path === n)}, 'light']">{{ k }}</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from '@/api'
import jsonp from 'jsonp'
export default {
  name: 'header',
  data () {
    return {
      nav: {'帮助中心': '/Help', '信息披露': '/Information', 'ICO评级': '/Level', '首页': '/'},
      logo: require('@/assets/images/logo.png'),
      fixedLogo: require('@/assets/images/fixed_logo.png')
    }
  },
  created () {
    var that = this
    window.addEventListener('scroll', this.test, false)
    jsonp('https://www.baoquan.com/ws/jsonP/login', null, function (err, data) {
      if (err) {
        console.error(err.message)
      } else {
        if (data.code === 200) {
          api.post('login', {data: data.data}).then(res => {
            that.$store.commit('setToken', res)
          })
        } else if (data.code === 401) {
          // api.post('login_out', {data: this.$store.state.user_id}).then(res => {
          //   console.log(res)
          // })
        } else {
          // api.post('login_out', {data: this.$store.state.user_id}).then(res => {
          //   console.log(res)
          // })
          alert('服务器错误！')
        }
      }
    })
  },
  methods: {
    test (e) {
      var ele = document.querySelector('.header')
      if (ele.classList.contains('fixed') && e.target.scrollingElement.scrollTop > 0) {
        ele.classList.add('bg_opacity')
      } else {
        ele.classList.remove('bg_opacity')
      }
    }
  }
}
</script>

<style>
  .fixed {
    left: 0;
    right: 0;
    z-index: 2
  }
  .white_nav a{
    color: #fff;
    opacity: 0.8
  }
  .white_nav .hover:hover a,.white_nav .hover a.black{
    color: #fff;
    opacity: 1
  }
  .bg_opacity{
    background: rgba(0,0,0,.7);
  }
</style>



