import Vue from 'vue'
import Router from 'vue-router'
const Home = resolve => require(['@/pages/Home'], resolve)
const Detail = resolve => require(['@/pages/Detail'], resolve)
const Introduction = resolve => require(['@/components/Detail/Introduction'], resolve)
const Grade = resolve => require(['@/components/Detail/Grade'], resolve)
const Stuation = resolve => require(['@/components/Detail/Stuation'], resolve)
const Activity = resolve => require(['@/components/Detail/Activity'], resolve)
const Comment = resolve => require(['@/components/Detail/Comment'], resolve)
const Information = resolve => require(['@/pages/Information'], resolve)
const Level = resolve => require(['@/pages/Level'], resolve)
const Help = resolve => require(['@/pages/Help'], resolve)

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/Detail/:proId',
      component: Detail,
      children: [
        { path: 'Introduction', name: 'introduction', component: Introduction },
        { path: 'Grade', name: 'grade', component: Grade },
        { path: 'Stuation', name: 'stuation', component: Stuation },
        { path: 'Activity', name: 'activity', component: Activity },
        { path: 'Comment', name: 'comment', component: Comment }
      ]
    },
    {
      path: '/Information',
      name: 'information',
      component: Information
    },
    {
      path: '/Level',
      name: 'level',
      component: Level
    },
    {
      path: '/Help',
      name: 'help',
      component: Help
    }
  ]
})



